import './../Travel.css';
function Travel({image, subcategory, title, text}) {
    return(
        <article className="Travel">
            <img className="Travel__image" src={image} alt=''></img>
            <div className="Travel__wrapper">

            <h6 className="Travel__subcategory">{subcategory}</h6>
            <h1 className="Travel__title">{title}</h1>
            <p className="Travel__text">{text}</p>
            </div>
        </article>
    )
}

export default Travel